.welcome{
    position: fixed;
    /* background-color: aqua; */
    background-image: url('../../assets/img/welcome.jpeg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    background-attachment: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

}

.welcome::after{
    content: "";
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background-color: var(--Emerald);
    opacity: 30%;
    z-index: 1;
}

.text{
    position: relative;
    
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3vw;
    width: max-content;
    height: max-content;
    /* outline: dashed darkblue 0.1vw; */
    justify-content: space-evenly;
    background-color: rgba(0, 0, 0, 0.699);
    border-radius: 10px;
    padding: 1vw;
}

.text *{
    /* outline: dashed darkblue 0.1vw; */
    color: aliceblue;
    z-index: 2;
    position: relative;
}

.title{
    display: flex;
    flex-direction: column;
    
    gap:0;
    background-color: transparent;
    width: max-content;
    height: max-content;
    top: 20%;
}

.title div{
    font-size: 7vw;
}

.desc{

    font-size: 2vw;
}
