.section1{
    position: relative;
    width: 100%;
    height: 40vw;
    background-color: var(--Black);
    color: aliceblue;
    z-index: 2;
    display: flex;
    flex-direction: row;

}


.bioTitle{
    position: relative;
    width: 100%;
    height: max-content;
    font-size: 2.6vw;
    display: flex;
    justify-content: center;
    
}


.info{
    position: absolute;
    display: flex;
    height: min-content;
    width: min-content;
    flex-direction: column;
    top: 30%;
    left: 10%;
    gap: 1vw;
    align-items: center;
}

.name{
    font-size: 2vw;
    height: min-content;
    width: max-content;
}

.age{
    font-size: 1vw;
    width: max-content;
    height: min-content;
}
.location{
    font-size: 1vw;
    width: max-content;
    height: min-content;
}

.photo{
    position: absolute;
    top: 20%;
    right: 10%;
    background-color: var(--Emerald);
    width: 20vw;
    height: 30vw;
}